import React from 'react'
import Form from '../Components/Form'
import Container from '../Components/Container'
import forgotSubmit from '../handlers/forgot'
import forgotForm from '../forms/forgotForm'
import forgotValidator from '../validators/forgot-validator'

const LoginPage = ({ history }) => (
  <Container>
    <Form
      className={`
        bg-white
        border
        col-sm-10
        offset-sm-1
        col-md-8
        offset-md-2
        col-lg-6
        offset-lg-3
        col-xl-4
        offset-xl-4
        mt-4
        mb-4
        p-4
      `}
      validate={forgotValidator}
      onSubmit={forgotSubmit({ history })}
      fields={forgotForm}
    />
  </Container>
)

export default LoginPage


