import React from 'react'
import Container from '../Components/Container'
import Stepper from '../Components/Stepper'

import withApplication from '../enhancers/withApplication'

const ApplicationThree = ({
  history,
  application,
  error,
  isLoading,
}) => (
  <Container>
    {(error || !application) && (
      <div
        className="alert alert-warning text-center border border-top-0"
        role="alert"
      >
        This is not a real application, but just an example. To send an application
        <button 
          onClick={() => history.push('/dashboard')} 
          type="button" 
          className="btn btn-secondary btn-sm ml-3"
          children="Click Here"
        />
      </div>
    )}
    <div
      className="col-lg-6 offset-lg-3 mt-4 mb-4 bg-white border pt-4 pl-5 pr-5 text-center"
    >
      <Stepper activeStep={2} />
      <h1 className="mb-4 text-center">You Are Almost Done!</h1>
      <div className="alert alert-success col-8 offset-2 mb-4">
        <h5 className="d-inline font-weight-bold">Last step:</h5> <u>Check your email.</u> You must give Experian (credit provider) permission for us to check your credit. Then you're done! An agent will reach out to you about results.
      </div>
      <div className="d-flex justify-content-center mb-4">
        <a
          target="_blank"
          href={'http://gmail.com'}
          className="btn btn-lg btn-primary white-text"
          children="Go To Email"
        />
      </div>
    </div>
  </Container>
)

export default withApplication(ApplicationThree)



