import React from 'react'
import stateData from '../helpers/state-data'

const addPropertyForm = [
  [{ render: () => <h2 className="mb-2 mt-2">Add a New Property</h2> }],
  [{ name: 'addressOne', type: 'text', label: 'Address:', placeholder: '8 South Congress Ln Suite 500' }],
  [{ name: 'city', type: 'text', label: 'City', placeholder: 'Austin' }, {
      name: 'state',
      label: 'State',
      render: ({ fieldList, handleChange, isSubmitting, touched, errors }) => (
        <select
          name="state"
          className="form-control"
          onChange={handleChange}>
          <option selected></option>
          {stateData.map((state) => <option value={state}>{state}</option>)}
        </select>
      )
    }, { name: 'zip', type: 'text', label: 'Zip', placeholder: '78787' }],
  [{ name: 'monthlyAsking', type: 'number', label: 'Monthly Asking (USD):', placeholder: '1720' }],
  [{ render: ({ handleSubmit, isSubmitting, touched, errors }) => (
    <div>
      <button
        className="btn btn-success col-md-12 mt-2"
        onClick={handleSubmit}
        children="Add Property"
        disabled={isSubmitting}
      />
      {touched && errors.form && (
        <p className="text-danger">{errors.form}</p>
      )}
    </div>
  )}],
]

export default addPropertyForm


