import React from 'react'
import { BrowserRouter, Switch, Route } from "react-router-dom"

import AdminPanel from './Components/AdminPanel'
import ExampleApp from './Components/ExampleApp'
import AboutUs from './Components/AboutUs'
import ContactUs from './Components/ContactUs'
import Home from './Components/Home'

import LoginPage from './pages/login-page'
import SignupPage from './pages/signup-page'
import ApplicationOne from './pages/application-1'
import ApplicationTwo from './pages/application-2'
import ApplicationThree from './pages/application-3'
import ForgotPage from './pages/forgot-page'
import ResetPage from './pages/reset-page'
import DashboardPage from './pages/dashboard-page'

import './App.css'

const App = () => (
  <BrowserRouter>
    <Switch>
      <Route exact path='/' component={Home} />
      <Route path='/login' component={LoginPage} />
      <Route path='/example' component={ExampleApp} />
      <Route path='/contact' component={ContactUs} />
      <Route path='/signup' component={SignupPage} />
      <Route path='/application1' component={ApplicationOne} />
      <Route path='/application2' component={ApplicationTwo} />
      <Route path='/application3' component={ApplicationThree} />
      <Route path='/forgot' component={ForgotPage} />
      <Route path='/reset/:resetToken' component={ResetPage} />
      <Route path='/dashboard' component={DashboardPage} />
      <Route path='/about' component={AboutUs} />
      <Route path='/adminpanel2268@5878' component={AdminPanel} />
    </Switch>
  </BrowserRouter>
)

export default App



