const statusData = {
  sent: {
    text: 'Sent',
    className: 'badge-secondary',
    message: 'The application has been sent to the applicants email, but they have not opened it yet.',
  },
  opened: {
    text: 'Opened',
    className: 'badge-warning',
    message: 'The applicant has opened the email, but has not looked at the application yet.',
  },
  viewed: {
    text: 'Viewed',
    className: 'badge-warning',
    message: 'The applicant has started the application.',
  },
  pending: {
    text: 'Pending',
    className: 'badge-warning',
    message: 'The applicant has finished the application, and we are just waiting for the results',
  },
  completed: {
    text: 'Completed',
    className: 'badge-success',
    message: 'Report ready for viewing!',
  }
}

export default statusData


