import React, { Component } from 'react';
import AppBar from './AppBar.js';

export default class AdminPanel extends Component {
  constructor(props){
    super(props);
    this.state={
      test: 'test',
      files: [1,2,3],
      status: null,
      failed: false,
      allApplications: [],
      selectedApplicant: [],
      selectedProperty: [],
    }
  }

componentWillMount() {

  this.setState({index: this.props.index})
  if (JSON.parse(localStorage.getItem('auth'))) {
    fetch("https://polar-caverns-23447.herokuapp.com/applications",
    {headers: {'authorization': `Token ${JSON.parse(localStorage.getItem('auth')).token}`}})
    .then((res)=>{
      return res.json();
    })
    .then((res)=>{
      if (res) {
        console.log('res is', res)
        res.forEach((ele)=>{
          console.log(ele.property.user)
        })
        this.setState({allApplications: res})
        console.log(this.state.allApplications)
      } else {
        this.setState({failed: true})
      }
    })
  }
}

// /properties/:property_id/applications/:application_id
submitFinal = () => {

      var config = {
          completed: true
        }
      fetch(("https://polar-caverns-23447.herokuapp.com/properties/"+ this.state.property_id + "/applications/" + this.state.application_id ), {
        "method": "PUT",
        "headers": {
          "content-type": "application/json",
          "authorization":  `Token ${JSON.parse(localStorage.getItem('auth')).token}`,
        },
        body: JSON.stringify(config)
      })
      .then(()=>{
        this.setState({ removeOpen: false });
        window.location.reload()
      })
      .catch((err)=>{
        throw err;
      })
  };

addPayment = () => {
  var temp = this.state.files;
  var num = temp[temp.length] +1;
  temp.push(num);
  this.setState({ files: temp });
}

selectThing(app, prop, i) {

  this.state.allApplications.forEach((ele, i) => {
    var thingy = ('listItem' + i)
    document.getElementById(thingy).style.color = "white"
    document.getElementById(thingy).style.backgroundColor = "black"
  })

  var thingy = ('listItem' + i)
  document.getElementById(thingy).style.color = "black"
  document.getElementById(thingy).style.backgroundColor = "white"
  this.setState({
                status: document.getElementById('status').value,
                 application_id: app,
                 property_id: prop,
                 })
}

  changeStatus() {
    var status = this.state.status;
    var prop_id = this.state.property_id;
    var app_id = this.state.application_id;

    var config = {
      status: status,
    }

      fetch(('https://polar-caverns-23447.herokuapp.com/properties/' + app_id +'/applications/' + prop_id), {
        method: 'PUT',
        headers: {
          'authorization': `Token ${JSON.parse(localStorage.getItem('auth')).token}`,
          'content-type': 'application/json'
        },
        body: JSON.stringify(config)
      })
      .then((res) => {
        console.log(res)
        window.location.reload();
      })
  }

  render() {
    return (
      <div>
      <AppBar />
      <div style={{backgroundColor: 'white', width: '70%', marginLeft: '15%', marginRight: '15%', marginTop: 15}}>
      <div style={{width: '100%', maxWidth: 700, margin: 'auto'}}>
        <button onClick={ ()=>{ console.log(this.state) } }>log</button>
        <h1>Admin Panel</h1>

        <table class="table table-striped table-dark">
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">Applicant</th>
              <th scope="col">Property</th>
              <th scope="col">Current Status</th>
              <th scope="col">Property ID</th>
              <th scope="col">Applicant ID</th>
            </tr>
          </thead>
          <tbody>

            {this.state.allApplications.map((ele, i) => {
              return (
                <tr id={'listItem'+i} onClick={ ()=>{ this.selectThing(ele.property._id, ele._id, i) } } >
                  <th scope="row">{i}</th>
                  <td id='applicantName'>{ele.applicantName}</td>
                  <td id="addressOne">{ele.property.addressOne}</td>
                  <td id='status'>{ele.status}</td>
                  <td id='propID'>{ele.property._id}</td>
                  <td id='appID'>{ele._id}</td>
                </tr>
              )
            })}

          </tbody>
        </table>

        {
          this.state.fail ?
                  <div class="alert alert-danger" role="alert">
                    GET Request failed to retrieve all applications!!!
                  </div> : null
        }
      </div>
        <div style={{width: '70%', marginLeft: '15%', marginRight: '15%'}}>
          <form style={{paddingBottom: 7, marginTop: 20}}>
            <div className="form-group">
            <br />
            <label htmlFor="exampleFormControlSelect1">Applicant Name</label>
            <select className="form-control" id="exampleFormControlSelect1">
              <option>...</option>
              <option>Gabe Trinidad</option>
              <option>Tristyn Leos</option>
              <option>Misha Hoo</option>
            </select>
              <br />

              <label htmlFor="exampleFormControlSelect1">Applicant Employment Status</label>
              <select className="form-control" id="exampleFormControlSelect1">
                <option>...</option>
                <option>Employed Full Time</option>
                <option>Employed Part Time</option>
                <option>Self Employed</option>
                <option>Unemployed</option>
              </select>
              <br/>
              <label htmlFor="exampleFormControlSelect1">Which property is this for?</label>
              <select className="form-control" id="exampleFormControlSelect1">
                  <option>...</option>
                  <option>123 Main Street</option>
                  <option>123 Main Street</option>
                  <option>123 Main Street</option>
                  <option>123 Main Street</option>
              </select>

            </div>
              <div className="form-group">
              <br/>

              <label htmlFor="exampleFormControlFile1">Upload Photo ID</label>
                <div style={{border: '1px solid silver', borderRadius: 4}}>
                  <div>
                    <input style={{margin: 5}} type="file" className="form-control-file" id="exampleFormControlFile1" />
                  </div>
                </div>
              <br/>

              <label htmlFor="exampleFormControlFile1">Upload Background Check</label>
                <div style={{border: '1px solid silver', borderRadius: 4}}>
                  <div>
                    <input style={{margin: 5}} type="file" className="form-control-file" id="exampleFormControlFile1" />
                  </div>
                </div>
              <br/>

              <label htmlFor="exampleFormControlFile1">Upload Credit Report</label>
                <div style={{border: '1px solid silver', borderRadius: 4}}>
                  <div>
                    <input style={{margin: 5}} type="file" className="form-control-file" id="exampleFormControlFile1" />
                  </div>
                </div>
              <br/>

              <div className="form-group">

              <label htmlFor="exampleFormControlFile1">Upload at least 6 months of paystubs.</label>
              <div style={{border: '1px solid silver', borderRadius: 4}}>
              {
               this.state.files.map((ele, i) => {
                 return (
                   <div >
                     <input key={'adminPaystub' + i} style={{margin: 5}} type="file" className="form-control-file" id="exampleFormControlFile1" />
                   </div>
                 )
               })
              }
              <div style={{marginLeft: 5, marginBottom: 5}}>
                <button onClick={ ()=>{ this.addPayment() } }  type="button" className="btn btn-secondary btn-sm">Add Additional Paystub</button>
              </div>
              </div>
              </div>
              <button onClick={ ()=>{  } } type="button" className="btn btn-danger">Finalize Application</button>
              </div>
            </form>
        </div>
        <div style={{paddingBottom: 10, width: '30%', marginLeft: '35%', marginRight: '35%'}}>
        </div>
      </div>
      <br/>
      <div style={{backgroundColor: 'white', width: '70%', marginLeft: '15%', marginRight: '15%', marginTop: 30, marginBottom: 30}}>
        <div style={{width: '70%', marginLeft: '15%', marginRight: '15%', marginTop: 10}}>

        <label htmlFor="exampleFormControlSelect1">New Status:</label>
          <select onChange={ ()=>{ this.setState({status: document.getElementById('appStatus').value}) } } className="form-control" id="appStatus">
              <option>...</option>
              <option>sent</option>
              <option>opened</option>
              <option>viewed</option>
              <option>pending</option>
              <option>completed</option>
          </select>
          <button onClick={ ()=>{ this.changeStatus() } } style={{marginBottom: 20}} type="button" className="btn btn-primary">Change Status</button>
          <br/>
          <table class="table table-striped table-dark">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">Applicant</th>
                <th scope="col">Property</th>
                <th scope="col">Current Status</th>
                <th scope="col">Property ID</th>
                <th scope="col">Applicant ID</th>
              </tr>
            </thead>
            <tbody>

              {this.state.allApplications.map((ele, i) => {
                return (
                  <tr id={'listItem'+i} onClick={ ()=>{ this.selectThing(ele.property._id, ele._id, i) } } >
                    <th scope="row">{i}</th>
                    <td id='applicantName'>{ele.applicantName}</td>
                    <td id="addressOne">{ele.property.addressOne}</td>
                    <td id='status'>{ele.status}</td>
                    <td id='propID'>{ele.property._id}</td>
                    <td id='appID'>{ele._id}</td>
                  </tr>
                )
              })}

            </tbody>
          </table>
          </div>
        </div>
      </div>
    );
  }
}



