import qs from 'query-string'
import { mapProps } from 'recompose'

const withQueryParser = mapProps(({ history, location, ...rest }) => {
  const queryString = new URLSearchParams(location.search)
  const pending = queryString.get('pending') || 'true'
  const completed = queryString.get('completed') || 'false'
  const runDemo = queryString.get('runDemo') || '0'
  const resource = queryString.get('resource') || 'applications'
  return {
    history,
    location,
    pending,
    completed, 
    runDemo,
    resource,
    setQuery: (props) => history.push(`/dashboard?${qs.stringify({ pending, completed, runDemo, resource, ...props })}`),
    ...rest,
  }
})

export default withQueryParser



