import React from 'react'
import { Link } from 'react-router-dom'
import Modal from 'react-bootstrap4-modal'
import { compose } from 'recompose'
import StripeCheckout from 'react-stripe-checkout'
import { STRIPE_KEY } from '../config'

import Stepper from '../Components/Stepper'
import Container from '../Components/Container'

import paymentHandler from '../handlers/payment'
import withApplication from '../enhancers/withApplication'
import withDialog from '../enhancers/withDialog'
import ifThen from '../enhancers/ifThen'

const ApplicationTwo = ({
  history,
  location,
  application,
  error,
  isLoading,
  showModal,
  hideModal,
  modal,
}) => (
  <Container>
    {(error || !application) && (
      <div
        className="alert alert-warning text-center border border-top-0"
        role="alert"
      >
        This is not a real application, but just an example. To send an application
        <button 
          onClick={() => history.push('/dashboard')} 
          type="button" 
          className="btn btn-secondary btn-sm ml-3"
          children="Click Here"
        />
      </div>
    )}
    <div
      className="col-lg-6 offset-lg-3 mt-4 mb-4 bg-white border pt-4 pl-5 pr-5 text-center"
    >
      <Stepper activeStep={1} />
      <h1 className="mb-4">Payment</h1>
      <h4 className="mb-4">{(application && application.applicantName) || 'John Appleseed'}</h4>
      <h5 className="mb-4 text-secondary">{(application && application.property.addressOne) || '8 Hacker Lane'}</h5>

      <StripeCheckout
         token={paymentHandler({ showModal, history, applicationId: application && application._id })}
         stripeKey={STRIPE_KEY}
         className="mb-4"
         locale="auto"
         zip-code="true"
         description="Credit/Background Check Payment"
         amount={5000}
         allowRememberMe={false}
       />

     <div className="d-flex justify-content-center  mb-5">
      <Link
        style={{ width: 120 }}
        className="btn btn-primary text-white d-block"
        to={`/application1${application ? `?application_id=${application._id}` : ''}`}
        children="Back"
      />
     </div>
    </div>
    <Modal
      onClickBackdrop={hideModal}
      visible={modal.visible}
      children={modal.children ? modal.children({ showModal, hideModal }) : ""}
    />
  </Container>
)

export default compose(
  withApplication,
  withDialog,
  ifThen(({ application }) => (application && application.charge && application.charge.status === 'succeeded'), ({ history, application }) => history.push(`/application3?application_id=${application._id}`)),
)(ApplicationTwo)



