// if (!(values.password === values.confirmPassword)) errors.form = 'Password does not match.'

import { API_URL } from '../config'

const resetSubmit = ({ history, match }) => (values, { setSubmitting, setFieldError, setErrors, ...rest }) => {
  const resetToken = match.params.resetToken
  console.log(match)
  if (values.password !== values.confirmPassword) {
    setSubmitting(false)
    return setFieldError('form', 'Password does not match.')
  }

  if (resetToken) {
    return fetch(`${API_URL}/reset/${resetToken}`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ password: values.password })
    })
    .then((res) => res.json())
    .then((res) => {
      setSubmitting(false)
      if (res.errors) return setFieldError('form', res.errors.message)
      history.push('/login')
    })
  }
}

export default resetSubmit


