import React from 'react'
import classNames from 'classnames'

let STEPS = [
  { label: 'Tenant Information'},
  { label: 'Payment'},
  { label: 'Credit Check'},
]

const Step = ({ activeStep }) => ({ label }, index) => (
  <div 
    key={index}
    className={classNames(
      'mdl-stepper-step', 
      (index < activeStep && 'step-done'),
      (index <= activeStep && 'active-step')
    )}
  >
    <div className="mdl-stepper-circle"><span>{index + 1}</span></div>
    <div className="mdl-stepper-title">{label}</div>
    <div className="mdl-stepper-bar-left"></div>
    <div className="mdl-stepper-bar-right"></div>
  </div>
)

const Stepper = ({
  steps = STEPS,
  activeStep = 0,
}) => (
  <div className="mdl-stepper-horizontal-alternative">
    {steps.map(Step({ activeStep }))}
  </div>
)

export default Stepper



