import React from 'react'
import Form from '../Components/Form'
import Container from '../Components/Container'

import application1Form from '../forms/application1Form'
import { updatePropertyApplication } from '../handlers/applications'
import withApplication from '../enhancers/withApplication'

const Application1 = ({
  history,
  location,
  application,
  error,
  isLoading,
}) => (
  <Container>
    {(error || !application) && (
      <div
        className="alert alert-warning text-center border border-top-0"
        role="alert"
      >
        This is not a real application, but just an example. To send an application
        <button 
          onClick={() => history.push('/dashboard')} 
          type="button" 
          className="btn btn-secondary btn-sm ml-3"
          children="Click Here"
        />
      </div>
    )}
    <Form
      initialValues={{ payStubs: [undefined, undefined, undefined] }}
      className="col-lg-6 offset-lg-3 mt-4 mb-4 bg-white border pt-4 pl-5 pr-5"
      onSubmit={(
        application 
        ? updatePropertyApplication({ history, application }) 
        : () => history.push('/application2')
      )}
      fields={application1Form}
      meta={{ application }}
    />
  </Container>
)

export default withApplication(Application1)


