import React from 'react'

const applicationClickHelp = ({
  message
}) => ({
  showModal,
  hideModal,
  visible,
}) => (
  <div>
    <div className="modal-header">
      <h5>Help</h5>
    </div>
    <div className="modal-body">
      <p>{message}</p>
    </div>
    <div className="modal-footer">
      <button
        type="button"
        className="btn btn-primary"
        onClick={hideModal}
        children="Close"
      />
    </div>
  </div>
)

export default applicationClickHelp


