const fields = [
  ['addressOne', 'Address'],
  ['city', 'City'],
  ['state', 'State'],
  ['zip', 'Zip'],
  ['monthlyAsking', 'Monthly Asking'],
]

const addPropertyValidator = (values) => {
  let errors = {}
  for (const v of fields) {
    let [name, label] = v
    if (!values[name]) errors[name] = `${label} is required.`
  }
  return errors
}

export default addPropertyValidator


