import React from 'react'
import { Link } from 'react-router-dom'

const loginForm = [
  [{ render: () => <h2 className="mb-4 mt-4">Login to ApplyNowLeasing</h2> }],
  [{ name: 'email', type: 'email', label: 'Email:', placeholder: 'Email' }],
  [{ name: 'password', type: 'password', label: 'Password:', placeholder: 'Password' }],
  [{ render: ({ handleSubmit, isSubmitting }) => (
    <button
      className="btn btn-primary col-md-12 mt-2 mb-2"
      onClick={handleSubmit}
      children="Login"
      disabled={isSubmitting}
    />
  )}],
  [{ render: () => (
    <a
      className="text-primary d-block mt-1"
      href="/signup" 
      children="Don't have an account? Signup here."
    />
  )}],
  [{ render: () => (
    <Link
      to="/forgot"
      className="text-primary d-block mt-1"
      children="Forgot Password?"
    />
  )}],
  [{ render: ({ touched, errors }) => (
    touched && errors.form && (
      <p className="text-danger">{errors.form}</p>
    )
  )}]
]

export default loginForm


