import React, { Component } from 'react';
import step1 from './images/step1.png';
import step2 from './images/step2.png';
import step3 from './images/step3.png';

export default class VideoHolder extends Component {
  constructor(props){
    super(props);
    this.state= {

    }
  }

  render() {
    return (
      <div style={{height: 'auto', padding: 20, backgroundColor: '#dbdbdb'}}>

        <div style={{backgroundColor: 'white', height: 'auto', marginTop: 10, width: '80%', marginLeft: '10%', marginRight: '10%'}}>
          <div style={{width: '80%', marginLeft: '10%', marginRight: '10%'}} id="carouselExampleIndicators" className="carousel slide" data-ride="carousel">
          <h1 style={{color: '#727272', fontWeight: 'bold', textAlign: 'center', width: '60%', marginLeft: '20%', marginRight: '20%'}}>How It Works</h1>
            <ol className="carousel-indicators">
              <li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>
              <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
              <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
              <li data-target="#carouselExampleIndicators" data-slide-to="3"></li>
            </ol>
            <div style={{marginBottom: 35}} className="carousel-inner">
              <div style={{marginBottom: 30}} className="carousel-item active">
              <div style={{textAlign: 'center', width: '60%', marginRight: '20%', marginLeft: '20%'}}>
              <h3 style={{color: 'black'}}>Step One: Add a new property</h3>
              <p style={{color: 'black'}}>Add the property once, and send an unlimited number of applications. You never pay a penny.</p>
              </div>
                <img className="d-block w-100" src={step1} alt="Enter a new listing" />
                <div className="carousel-caption d-none d-md-block">
                </div>
              </div>
              <div style={{marginBottom: 20}}  className="carousel-item">
              <div style={{textAlign: 'center', width: '60%', marginRight: '20%', marginLeft: '20%'}}>
              <h3 style={{color: 'black'}}>Step Two: Send Applications</h3>
              <p style={{color: 'black'}}>All you need is the tenants name and email. That's it!</p>
              </div>
                <img className="d-block w-100" src={step2} alt="Second slide" />
                <div className="carousel-caption d-none d-md-block">
                </div>
              </div>
              <div style={{marginBottom: 20}}  className="carousel-item">
                <div style={{textAlign: 'center', width: '60%', marginRight: '20%', marginLeft: '20%'}}>
                <h3 style={{color: 'black'}}>Step 3: Applicants Fill Out The Application</h3>
                <p style={{color: 'black'}}>After you send an application, tenants will run and pay for their own background checks.</p>
                </div>
                <img className="d-block w-100" src={step3} alt="Third slide" />
                <div className="carousel-caption d-none d-md-block">
                </div>
              </div>
              <div style={{marginBottom: 20}}  className="carousel-item">
              <div style={{textAlign: 'center', width: '60%', marginRight: '20%', marginLeft: '20%'}}>
              <h3 style={{color: 'black'}}>Step 4: Receive Your Report</h3>
              <p style={{color: 'black'}}>As soon as the application is completed by the tenant, your report will be available.</p>
              </div>
                <img className="d-block w-100" src={step3} alt="Third slide" />
              </div>
            </div>
            <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
              <span className="carousel-control-prev-icon" aria-hidden="true"></span>
              <span className="sr-only">Previous</span>
            </a>
            <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
              <span className="carousel-control-next-icon" aria-hidden="true"></span>
              <span className="sr-only">Next</span>
            </a>
          </div>
        </div>
      </div>
    );
  }
}



