import { API_URL } from '../config'

const forgotSubmit = ({ history }) => (values, { setSubmitting, setFieldError, setErrors, setStatus, ...rest }) => {
  return fetch(`${API_URL}/forgot`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(values)
  })
  .then((res) => res.json())
  .then((res) => {
    if (res.errors) {
      setTimeout(() => setErrors({}), 2000)
      return setFieldError('form', res.errors.message)
    }
    
    setStatus({ confirmed: true })
  })
}

export default forgotSubmit


