import React from 'react'
import Container from '../Components/Container'
import Form from '../Components/Form'
import resetSubmit from '../handlers/reset'
import resetForm from '../forms/resetForm'
import resetValidator from '../validators/reset-validator'

const ResetPage = ({ history, match }) => (
  <Container>
    <Form
      className={`
        bg-white
        border
        col-sm-10
        offset-sm-1
        col-md-8
        offset-md-2
        col-lg-6
        offset-lg-3
        col-xl-4
        offset-xl-4
        mt-4
        mb-4
        p-4
      `}
      validate={resetValidator}
      onSubmit={resetSubmit({ history, match })}
      fields={resetForm}
    />
  </Container>
)

export default ResetPage


