import React from 'react'
import stateData from '../helpers/state-data'

const signupForm = [
  [{ render: () => <h2 className="mb-4 mt-4">Signup to ApplyNowLeasing</h2> }],
  [{ name: 'firstName', type: 'text', label: 'First Name:', placeholder: 'John' }, { name: 'lastName', type: 'text', label: 'Last Name:', placeholder: 'Doe' }],
  [{ name: 'email', type: 'email', label: 'Email:', placeholder: 'Email' }],
  [{ name: 'password', type: 'password', label: 'Password:' }],
  [{ name: 'confirmPassword', type: 'password', label: 'Confirm Password:' }],
  [{ name: 'city', type: 'text', label: 'City', placeholder: 'Austin' }, {
    name: 'state',
    label: 'State',
    render: ({ fieldList, handleChange, isSubmitting, touched, errors }) => (
      <select
        name="state"
        className="form-control"
        onChange={handleChange}>
        <option defaultValue></option>
        {stateData.map((state, i) => <option key={i} value={state}>{state}</option>)}
      </select>
    )
  }, { name: 'zip', type: 'text', label: 'Zip', placeholder: '78787' }],
  [{ render: ({ handleSubmit, isSubmitting }) => (
    <button
      className="btn btn-success col-md-12 mt-2 mb-2"
      onClick={handleSubmit}
      children="Signup"
      disabled={isSubmitting}
    />
  )}], 
  [{ render: () => (
    <a
      className="text-primary d-block"
      href="/login" 
      children="Already have an account? Login here."
    />
  )}],
  [{ render: ({ touched, errors }) => (
    touched && errors.form && (
      <p className="text-danger mt-1">{errors.form}</p>
    )
  )}],
  [{ render: ({ isSubmitting }) => (
    isSubmitting && (
      <div className="progress mb-2 mt-2">
        <div 
          className="progress-bar progress-bar-striped progress-bar-animated" 
          role="progressbar" 
          aria-valuenow="100" 
          aria-valuemin="0" 
          aria-valuemax="100" 
          style={{width: '100%'}}
        />
      </div>
    )
  )}],
]

export default signupForm


