import React from 'react'
import classNames from 'classnames'
import { Formik } from 'formik'

const Input = ({ values, fieldList, handleChange, isSubmitting, setFieldValue, setStatus, status, meta, touched, errors }) => ({ className, name, label, type, placeholder, Tag = 'input', render }) => (
  render ? (
    render({ values, fieldList, handleChange, isSubmitting, setFieldValue, setStatus, status, meta, touched, errors })
  ) : (
    <Tag
      className={classNames("form-control", className)}
      name={name}
      type={type}
      onChange={handleChange}
      placeholder={placeholder}
    />
  )
)

const FormRow = ({ values, fieldList, handleChange, isSubmitting, setFieldValue, setStatus, status, meta, touched, errors }) => (
  fieldList.map(({ className, name, type, label, placeholder, Tag, render }, i) => (
    <div
      key={i}
      className={classNames(`form-group col-md-${12/fieldList.length}`, className)}>
      {label && (
        <label
          className="font-weight-bold"
          htmlFor={name}
          children={label}
        />
      )}
      {Input({ values, fieldList, handleChange, isSubmitting, setFieldValue, setStatus, status, meta, touched, errors })({ className, name, type, label, placeholder, Tag, render })}
      {touched && errors[name] && (
        <small className="form-text text-danger">{errors[name]}</small>
      )}
    </div>
  ))
)

const Form = ({
  className = "col-md-4 offset-md-4 mt-4",
  validate,
  onSubmit,
  fields,
  meta,
  ...rest,
}) => (
  <Formik
    validate={validate}
    onSubmit={onSubmit}
    {...rest}
    render={({
      values,
      errors,
      touched,
      dirty,
      handleChange,
      handleBlur,
      handleSubmit,
      isSubmitting,
      setFieldValue,
      setStatus,
      status,
    }) => (
      <form
        className={className}
        onSubmit={handleSubmit}
      >

        {fields.map((fieldList, i) => (
          <div key={i} className="form-row">
            {FormRow({
              values,
              errors,
              touched,
              dirty,
              handleChange,
              handleBlur,
              handleSubmit,
              isSubmitting,
              setFieldValue,
              setStatus,
              status,
              meta,
              fieldList,
            })}
          </div>
        ))}

      </form>
    )}
  />
)

export default Form



