const fields = [
  ['firstName', 'First Name'],
  ['lastName', 'Last Name'],
  ['email', 'Email'],
  ['password', 'Password'],
  ['confirmPassword', 'Confirm Password'],
  ['city', 'City'],
  ['state', 'State'],
  ['zip', 'Zip'],
]

const signupValidator = (values) => {
  let errors = {}
  for (const v of fields) {
    let [name, label] = v
    if (!values[name]) errors[name] = `${label} is required.`
  }
  return errors
}

export default signupValidator


