import { API_URL } from '../config'

let headers = () => ({
  'Content-Type': 'application/json',
  'Authorization': `Token ${(JSON.parse(localStorage.getItem('auth')) || {}).token}`,
})

export const addApplicationHandler = ({ hideModal, applicationsRefetch, property }) => (values, { setSubmitting, setFieldError, setErrors, ...rest }) => {
  fetch(`${API_URL}/properties/${property}/apply`, {
    method: 'POST',
    headers: headers(),
    body: JSON.stringify(values),
  })
  .then(res => res.json())
  .then((res) => {
    setSubmitting(false)

    if (res.errors) {
      setTimeout(() => setErrors({}), 2000)
      return setFieldError('form', res.errors.message)
    }
    
    hideModal()
    return applicationsRefetch()
  })
}

export const getNonCompleteApplications = () => {
  return fetch(API_URL + '/applications/my', {
    method: 'GET',
    headers: { 'Authorization': `Token ${JSON.parse(localStorage.getItem('auth')).token}` },
  })
  .then(res => res.json())
}

export const updatePropertyApplication = ({ history, application }) => (values) => {
  var formData = new FormData()
  
  for (const key in values) {
    if (key === 'payStubs') {
      values[key].forEach((file) => {
        file && formData.append(key, file)
      })
    }
    else formData.append(key, values[key])
  }

  fetch(`${API_URL}/properties/${application.property._id}/applications/${application._id}`, {
    method: 'PUT',
    body: formData
  })
  .then(res => res.json())
  .then(() => history.push(`/application2?application_id=${application._id}`))
}

window.getNonCompleteApplications = getNonCompleteApplications


