import { API_URL } from '../config'

let headers = () => ({
  'Content-Type': 'application/json',
  'Authorization': `Token ${(JSON.parse(localStorage.getItem('auth')) || {}).token}`,
})

export const addPropertyHandler = ({ hideModal, refetch }) => (values, { setSubmitting, setFieldError, setErrors, }) => {
  fetch(`${API_URL}/properties`, {
    method: 'POST',
    headers: headers(),
    body: JSON.stringify({ ...values, country: 'US' }),
  })
  .then(res => res.json())
  .then((res) => {
    setSubmitting(false)

    if (res.errors) {
      setTimeout(() => setErrors({}), 2000)
      return setFieldError('form', res.errors.message)
    }
    
    hideModal()
    return refetch()
  })
}

export const deactivateProperty = ({ property, hideModal, refetch }) => () => {
  fetch(`${API_URL}/properties/${property}`, {
    method: 'PUT',
    headers: headers(),
    body: JSON.stringify({ completed: true }),
  })
  .then(res => res.json())
  .then((res) => {
    hideModal()
    refetch()
  }) 
}

export const activateProperty = ({ property, hideModal, refetch }) => () => {
  fetch(`${API_URL}/properties/${property}`, {
    method: 'PUT',
    headers: headers(),
    body: JSON.stringify({ completed: false }),
  })
  .then(res => res.json())
  .then((res) => {
    hideModal()
    refetch()
  }) 
}


