import React from 'react'
import TimeAgo from 'react-timeago'
import propertyButtonActionData, { propertyButtonActionDataInactive } from '../helpers/property-button-action-data'

const actionButton = ({ label, ...rest }, i) => (
  <button
    key={i}
    children={label}
    { ...rest }
  />
)

const PropertyItem = ({ showModal, hideModal, applicationsRefetch, refetch }) => ({
  _id,
  addressOne,
  city,
  state,
  zip,
  monthlyAsking,
  createdAt,
  completed,
}, i) => (
<div key={i} className="card">
  <div className="card-body">
    <div className="d-flex justify-content-between">
      <span className="font-weight-bold">{addressOne}</span>
      <small className="text-secondary">
        <TimeAgo date={createdAt} />
      </small>
    </div>
    <span className="d-block text-secondary">{city} {state} {zip}</span>
    <small className="text-secondary">$ {monthlyAsking}</small>
    <hr />
    {completed ? (
      propertyButtonActionDataInactive({ showModal, hideModal, applicationsRefetch, property: _id, refetch }).map(actionButton)
    ) : (
      propertyButtonActionData({ showModal, hideModal, applicationsRefetch, property: _id, refetch }).map(actionButton)
    )}
  </div>
</div>
)

export default PropertyItem


