import { API_URL } from '../config'
import confirmModal from '../modals/confirmModal'

const paymentHandler = ({
  showModal,
  history,
  applicationId,
}) => (tokenObj) => {
  fetch(`${API_URL}/applications/${applicationId}/charge`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(tokenObj)
  })
  .then((res) => res.json())
  .then((res) => {
    if (res.errors) showModal({ children: confirmModal({ bodyText: res.errors.message, okText: 'Got it!' }) })
    else history.push('/application3')
  })
}

export default paymentHandler


