import React from 'react'
import Modal from 'react-bootstrap4-modal'
import Media from 'react-media'
import { compose, renameProp } from 'recompose'

import AppBar from '../Components/AppBar'
import ApplicationList from '../Components/ApplicationList'
import PropertyList from '../Components/PropertyList'
import MobileView from '../Components/MobileView'

import withResource from '../enhancers/withResource'
import withQueryParser from '../enhancers/withQueryParser'
import withDialog from '../enhancers/withDialog'

const DashboardPage = ({
  applications,
  applicationsRefetch,

  properties,
  propertiesRefetch,

  pending,
  completed,
  resource,
  setQuery,

  showModal,
  hideModal,
  modal,
}) => (
  <div className="h-100 bg-default">
    <AppBar />
    <Media query={{ maxWidth: 599 }}>
      {matches =>
        matches ? (
          <MobileView
            resource={resource}
            setQuery={setQuery}
            applications={applications}
            properties={properties}
            showModal={showModal}
            hideModal={hideModal}
            applicationsRefetch={applicationsRefetch}
            refetch={propertiesRefetch}
          />
        ) : (
          <div className="row no-gutters" style={{ height: 'calc(100% - 96px)' }}>
            <div className="col-3 h-100">
              <ApplicationList
                applications={applications}
                pending={pending}
                setQuery={setQuery}
                showModal={showModal}
              />
            </div>
            <div className="col-9 h-100">
              <PropertyList
                properties={properties}
                completed={completed}
                setQuery={setQuery}
                showModal={showModal}
                hideModal={hideModal}
                applicationsRefetch={applicationsRefetch}
                refetch={propertiesRefetch}
              />
            </div>
          </div>
        )
      }
    </Media>
    <Modal
      onClickBackdrop={hideModal}
      visible={modal.visible}
      children={modal.children ? modal.children({ showModal, hideModal }) : ""}
    />
  </div>
)

export default compose(
  withResource({ resource: 'applications/my', resourceName: 'applications', refetchName: 'applicationsRefetch' }),
  withResource({ resource: 'properties', resourceName: 'properties', refetchName: 'propertiesRefetch' }),
  withQueryParser,
  withDialog,
)(DashboardPage)



