import React from 'react'
import { Link } from 'react-router-dom'

const Header = () => (
  <div
    id='app-background'
    className="d-flex flex-column align-items-center justify-content-center bg-success"
  >
    <h1
      className="text-white pb-2 font-weight-bold strokeme"
      children="Free Real Estate Agent Website"
    />
    <h4
      className="text-white font-weight-normal pb-4 strokeme"
      children="Easily check credit reports, criminal history and more."
    />
    <Link
      style={{ width: 250 }}
      className="btn btn-success white-text btn-lg p-3 border"
      to="/signup"
      children="Get Started."
    />
  </div>
)

export default Header


