import { compose, lifecycle } from 'recompose'
import { API_URL } from '../config'

const withApplication = compose(
  lifecycle({
    state: { application: null, error: null, isLoading: true },
    componentDidMount() {
      const query = new URLSearchParams(this.props.location.search)
      const applicationId = query.get('application_id')
      if (applicationId) {
        fetch(`${API_URL}/applications/${applicationId}`)
          .then((res) => {
            if (!res.ok) throw new Error()
            else return res.json()
          })
          .then((application) => this.setState({ application, isLoading: false }))
          .catch(() => this.setState({ error: true, isLoading: false }))
      }
    }
  })
)

export default withApplication


