import React from 'react'
import Stepper from '../Components/Stepper'

const EMPLOYMENT_STATUSES = ['fulltime', 'parttime', 'selfemployed', 'unemployed']

const application1Form = [
  [{ render: () => <Stepper /> }],
  [{ render: () => <h1 className="mb-3">Credit/Background Application</h1> }],
  [{
    render: ({ meta: { application } }) => <h4 className="mb-2">{(application && application.applicantName) || 'John Appleseed'}</h4>
  }, {
    render: ({ meta: { application }}) => <h5 className="mb-2 text-secondary text-md-right">{(application && application.property.addressOne) || '8 Hacker Ln'}</h5>
  }],
  [{ render: () => <hr /> }],
  [{ className: 'mb-5', name: 'employmentStatus', label: 'Employment Status', render: ({ fieldList, handleChange, isSubmitting, touched, errors }) => (
    <select
      name="employmentStatus"
      className="form-control"
      onChange={handleChange}>
      <option defaultValue></option>
      {EMPLOYMENT_STATUSES.map((status) => <option key={status} value={status}>{status}</option>)}
    </select>
  )}],
  [{ className: 'mb-5', name: 'photoId', label: 'Upload Photo Identification (Smartphone photos are acceptable)', render: ({ fieldList, handleChange, isSubmitting, setFieldValue, touched, errors }) => (
    <input
      name="photoId"
      type="file"
      className="form-control-file"
      onChange={(e) => setFieldValue('photoId', e.target.files[0])}
    />
  )}],
  [{ className: 'mb-3', name: 'payStubs', label: 'Upload Atlease 3 Months of Pay Stubs (Smartphone photos are acceptable)', render: ({ values, fieldList, handleChange, isSubmitting, setFieldValue, touched, errors }) => (
    <div>
      {values.payStubs.map((_, i) => (
        <input
          key={i}
          name="payStubs"
          type="file"
          className="form-control-file mb-3"
          onChange={(e) => {
            let payStubsCopy = values.payStubs.slice()
            payStubsCopy[i] = e.target.files[0]
            setFieldValue('payStubs', payStubsCopy)
          }}
        />
      ))}
      <button
        style={{ width: 99 }}
        className="btn btn-secondary btn-sm"
        onClick={(e) => {
          e.preventDefault()
          setFieldValue('payStubs', values.payStubs.slice().concat(undefined))
        }}
        children="Add File"
      />
    </div>
  )}],
  [{ render: () => <hr /> }],
  [{ render: ({ handleSubmit, isSubmitting }) => (
    <button
      className="btn btn-primary col-md-12 mb-2"
      onClick={handleSubmit}
      children="Next"
      disabled={isSubmitting}
    />
  )}], 
  [{ render: ({ touched, errors }) => (
    touched && errors.form && (
      <p className="text-danger mt-1">{errors.form}</p>
    )
  )}],
]

export default application1Form


