import React from 'react'

const confirmModal = ({
  headerText = 'Confirm',
  bodyText,
  okText,
  cancelText,
  onOk,
  onCancel,
  renderBody,
}) => ({
  showModal,
  hideModal,
}) => (
  <div>
    <div className="modal-header">
      <h5 children={headerText} />
    </div>
    <div className="modal-body">
      <p children={bodyText} />
    </div>
    {(okText || cancelText) && (
      <div className="modal-footer">
        {okText && (
          <button
            type="button"
            className="btn btn-sm w-25 btn-primary"
            onClick={onOk ? onOk : hideModal}
            children={okText}
          />
        )}

        {cancelText && (
          <button
            type="button"
            className="btn btn-sm w-25 btn-danger"
            onClick={onCancel ? onCancel : hideModal}
            children={cancelText}
          />
        )}
      </div>
    )}
  </div>
)

export default confirmModal


