import React, { Component } from 'react'
import Container from '../Components/Container'
import map from './images/austinMap.png'

const Contact = () => (
  <Container>
    <h1 style={{fontWeight: 'bold', textAlign: 'center', width: '40%', marginRight: '30%', marginLeft: '30%', marginTop: 30}}>Contact</h1>

    <h5 style={{textAlign: 'center', marginBottom: 30}}>Monday-Friday 10am(CST) - 8pm(CST)</h5>

      <div style={{marginBottom: 50, backgroundColor: '#f8f8f8', display: 'flex', width: '70%', marginLeft: '15%', marginRight: '15%', textAlign: 'center'}}>

        <div style={{flex: 1, margin: 10, textAlign: 'center'}}>
          <img alt="" style={{height: 350, border: '2px solid #c6c6c6'}} src={map}/>
        </div>

          <div style={{backgroundColor: '#f8f8f8', height: 350, margin: 10, textAlign: 'center', flex: 1}}>
            <div style={{textAlign: 'center'}}>
            <h3 style={{marginTop: 40, fontWeight: 'bold'}}>Apply Now Leasing</h3>
              <p>Austin, Texas</p>
              <div style={{marginLeft: '25%', textAlign: 'left'}}>
                <p><img alt="" style={{height: 25}} src="http://www.oogazone.com/wp-content/uploads/top-10-email-address-icon-clipart-cdr.jpg" /> Contact@ApplyNowLeasing.com</p>
                <p><img alt="" style={{height: 30}} src='https://en.vinex.market/files/65_address_icon.png' />1835 Burton Drive, STE #119-L</p>
                <p><img alt="" style={{height: 30}} src='https://cdn3.iconfinder.com/data/icons/trico-circles-solid/24/Circle-Solid-Phone-512.png' /> (512) 956-1160</p>
              </div>
          </div>

        </div>
      </div>
  </Container>
)

export default Contact


