import React from 'react'
import { NavLink, withRouter } from 'react-router-dom'
import Logo from '../Components/images/inlineLogo.png'

const AppBar = ({
  history,
  user = {},
}) => (
  <nav 
    style={{ flexShrink: 0 }}
    className="d-block d-md-flex nav-pills pl-2 pb-2 pr-2 bg-white border align-items-center"
  >
    <img
      alt=""
      className="cursor-pointer"
      onClick={() => history.push('/')}
      style={{ marginRight: 20, height: 80 }}
      src={Logo}
    />
    <div className="d-flex justify-content-between f-1">
      <div className="d-flex flex-row pt-3">
        <NavLink
          exact
          className="nav-link"
          to="/application1"
          children="Sample Application"
        />
      </div>
      <div className="d-flex flex-row pt-3">
        {localStorage.getItem('auth') ? ([
          <NavLink
            key="1"
            className="nav-link d-flex align-items-center mr-2"
            to="/dashboard"
            children="Dashboard"
            exact
          />,
          <NavLink
            key="2"
            className="nav-link d-flex align-items-center bg-danger text-white"
            to="/login"
            onClick={() => localStorage.removeItem('auth')}
            children="Logout"
          />
        ]) : ([
          <NavLink
            key="1"
            className="nav-link d-flex align-items-center mr-2"
            to="/login"
            children="Login"
            exact
          />,
          <NavLink
            key="2"
            className="nav-link d-flex align-items-center"
            to="/signup"
            children="Signup"
            exact
          />
        ])}
      </div>
    </div>
  </nav>
)

export default withRouter(AppBar)


