import React from 'react'
import AppBar from '../Components/AppBar'
import Footer from '../Components/Footer'

const Container = ({ children }) => (
  <div className="w-100 h-100 d-flex flex-column">
    <AppBar />
    <div className="f-1">
      {children}
    </div>
    <Footer />
  </div>
)

export default Container


