import { withState, compose, withHandlers } from 'recompose'

const INITIAL_STATE = { visible: false, children: null }

const withDialog = compose(
  withState('modal', 'setModal', INITIAL_STATE),
  withHandlers({
    showModal: ({ setModal }) => ({ children }) => setModal({ visible: true, children }),
    hideModal: ({ setModal }) => () => setModal(INITIAL_STATE)
  }),
)

export default withDialog


