import React, { Component } from 'react';
import CreditOne from './images/exampleCredit(1).png'
import CreditTwo from './images/exampleCredit(2).png'
import CreditThree from './images/exampleCredit(3).png'
import CreditFour from './images/exampleCredit(4).png'
import CriminalOne from './images/exampleCriminal(1).png'
import CriminalTwo from './images/exampleCriminal(2).png'
import IncomeOne from './images/examplePay(1).png'
import PhotoId from './images/exampleId(2).png'



export default class Login extends Component {
  constructor(props){
    super(props);
    this.state = {
    }
  }


  // Credit Report, Criminal Background, Paystubs, ID,
  render(){
    return (
      <div>

        <div style={{width: '80%', marginLeft: '10%', marginRight: '10%', backgroundColor: '#e5e5e5', paddingTop: 20, paddingBottom: 20, marginTop: 20, border: "2px solid #bcbcbc"}}>
          <h1 style={{textDecoration: 'underline', width: '70%', marginRight: '15%', marginLeft: '15%', textAlign: 'center', fontWeight: 'bold', fontSize: '2em'}}>Credit Report</h1>
            <img alt="" style={{width: '80%', marginRight: '10%', marginLeft: '10%', border: "2px solid #bcbcbc", marginTop: 20}} src={CreditOne} />
            <img alt="" style={{width: '80%', marginRight: '10%', marginLeft: '10%', border: "2px solid #bcbcbc", marginTop: 20}} src={CreditTwo} />
            <img alt="" style={{width: '80%', marginRight: '10%', marginLeft: '10%', border: "2px solid #bcbcbc", marginTop: 20}} src={CreditThree} />
            <img alt="" style={{width: '80%', marginRight: '10%', marginLeft: '10%', border: "2px solid #bcbcbc", marginTop: 20}} src={CreditFour} />
        </div>

        <div style={{width: '80%', marginLeft: '10%', marginRight: '10%', backgroundColor: '#e5e5e5', paddingTop: 20, paddingBottom: 20, marginTop: 20, border: "2px solid #bcbcbc"}}>
          <h1 style={{textDecoration: 'underline', width: '70%', marginRight: '15%', marginLeft: '15%', textAlign: 'center', fontWeight: 'bold', fontSize: '2em'}}>Criminal History</h1>
          <img alt="" style={{width: '60%', marginRight: '20%', marginLeft: '20%', border: "2px solid #bcbcbc", marginTop: 20}} src={CriminalOne} />
          <img alt="" style={{width: '60%', marginRight: '20%', marginLeft: '20%', border: "2px solid #bcbcbc", marginTop: 20}} src={CriminalTwo} />
        </div>

        <div style={{width: '80%', marginLeft: '10%', marginRight: '10%', backgroundColor: '#e5e5e5', paddingTop: 20, paddingBottom: 20, marginTop: 20, border: "2px solid #bcbcbc"}}>
          <h1 style={{textDecoration: 'underline', width: '70%', marginRight: '15%', marginLeft: '15%', textAlign: 'center', fontWeight: 'bold', fontSize: '2em'}}>Proof of Income</h1>
          <img alt="" style={{width: '60%', marginRight: '20%', marginLeft: '20%', border: "2px solid #bcbcbc", marginTop: 20}} src={IncomeOne} />
          <img alt="" style={{width: '60%', marginRight: '20%', marginLeft: '20%', border: "2px solid #bcbcbc", marginTop: 20}} src={IncomeOne} />
          <img alt="" style={{width: '60%', marginRight: '20%', marginLeft: '20%', border: "2px solid #bcbcbc", marginTop: 20}} src={IncomeOne} />
        </div>

        <div style={{width: '80%', marginLeft: '10%', marginRight: '10%', backgroundColor: '#e5e5e5', paddingTop: 20, paddingBottom: 20, marginTop: 20, marginBottom: 30, border: "2px solid #bcbcbc"}}>
          <h1 style={{textDecoration: 'underline', width: '70%', marginRight: '15%', marginLeft: '15%', textAlign: 'center', fontWeight: 'bold', fontSize: '2em'}}>Photo ID</h1>
          <img alt="" style={{width: '60%', marginRight: '20%', marginLeft: '20%', border: "2px solid #bcbcbc", marginTop: 20}} src={PhotoId} />
        </div>

      </div>
    )
  }
}



