import React from 'react'

import Appbar from '../Components/AppBar'
import Header from '../Components/Header'
import ReasonList from '../Components/ReasonList'
import Credits from '../Components/Credits'
import VideoHolder from '../Components/VideoHolder'
import Footer from '../Components/Footer'

const REASONS_1 = [
  ["/images/free.png", "Free"],
  ["/images/intuitive.png", "Intuitive"],
  ["/images/quick-and-easy.png", "Quick and Easy"],
  ["/images/tailored-results.png", "Tailored Results"],
]

const REASONS_2 = [
  ["/images/background-check.png", "Background Check"],
  ["/images/credit-report.png", "Credit Report"],
  ["/images/pay-stubs.png", "Pay Stubs"],
  ["/images/photo-id.png", "Photo ID"],
]

const App = ({
  history
}) => (
  <div className="bg-white">
    <Appbar />
    <Header />
    <ReasonList
      title="Reasons Why You'll Love Us"
      reasons={REASONS_1}
      rounded={true}
    />
    <VideoHolder />
    <ReasonList
      title="Your Report Will Include"
      reasons={REASONS_2}
    />
    <Credits
      history={history}
    />
    <Footer />
  </div>
)

export default App


