import { compose, lifecycle } from 'recompose'
import { API_URL } from '../config'
import qs from 'query-string'

let headers = () => ({
  'Content-Type': 'application/json',
  'Authorization': `Token ${(JSON.parse(localStorage.getItem('auth')) || {}).token}`,
})

const withResource = ({
  method = 'GET',
  query = {},
  body,
  resource,
  resourceName = 'data',
  refetchName = 'refetch',
  paramName,
}) => compose(
  lifecycle({
    fetch: function() {
      if (!resource) throw new Error('resource argument is required.')

      let resourceUrl = `${API_URL}/${resource}`
      if (paramName) resourceUrl += `/${this.props.match.params[paramName]}`
      if (Object.keys(query).length) resourceUrl += `/${qs.stringify(query)}`

      return fetch(resourceUrl, {
        method,
        headers: headers(),
        body: JSON.stringify(body)
      })
        .then((res) => res.json())
        .then((res) => {
          if (res.errors) this.setState({ isLoading: false, errors: res.errors })
          else this.setState({ isLoading: false, [resourceName]: res })
        })
        .catch((err) => {
          console.error(err)
          this.setState({ isLoading: false, errors: { message: 'Something Went Wrong.' } })
        })
    },
    state: { 
      data: null,
      error: null,
      isLoading: true,
    },
    componentDidMount() {
      this.setState({ [refetchName]: this.fetch.bind(this) }, () => this.fetch())
    }
  })
)

export default withResource



