import React from 'react'

const forgotForm = [
  [{ render: () => <h2 className="mt-4">Forgot Password</h2> }],
  [{ render: ({ status }) => (
    status && status.confirmed && (
      <div className="alert alert-success">
        <h6 className="d-inline font-weight-bold">Check your email.</h6> Steps to reset your password is in the email.
      </div>
    )
  )}],
  [{ name: 'email', type: 'email', label: 'Email:', placeholder: 'Email' }],
  [{ render: ({ handleSubmit, isSubmitting }) => (
    <button
      className="btn btn-success mt-2 mb-2"
      onClick={handleSubmit}
      children="Submit"
      disabled={isSubmitting}
    />
  )}],
  [{ render: ({ touched, errors }) => (
    touched && errors.form && (
      <p className="text-danger">{errors.form}</p>
    )
  )}],
]

export default forgotForm


