import React from 'react'
import classNames from 'classnames'
import confirmModal from '../modals/confirmModal'

import Form from '../Components/Form'
import addApplicationForm from '../forms/addApplicationForm'
import { addApplicationValidator } from '../validators/application-validator'
import { addApplicationHandler } from '../handlers/applications'
import { deactivateProperty, activateProperty } from '../handlers/properties'

const BASE_CLS = 'btn btn-sm text-white col-lg-5 mr-lg-2 mb-1 mb-lg-0'

let aAForm = ({ applicationsRefetch, property }) => ({ showModal, hideModal }) => (
  <div>
    <button
      type="button"
      className="close p-4"
      aria-label="Close"
      onClick={hideModal}
    >
      <span aria-hidden="true">&times;</span>
    </button>
    <Form
      className="p-4"
      fields={addApplicationForm}
      validate={addApplicationValidator}
      onSubmit={addApplicationHandler({ hideModal, applicationsRefetch, property })}
      meta={{ showModal, hideModal }}
    />
  </div>
)

const delConfirm = ({ onCancel }) => confirmModal({
  bodyText: `
    Are you sure you want to deactivate the property?
    You can always revert back later on.
  `,
  okText: 'Cancel',
  cancelText: 'Yes, deactivate!',
  onCancel: onCancel,
})

const actConfirm = ({ onOk }) => confirmModal({
  bodyText: `
    Are you sure you want to activate the property?
  `,
  okText: 'Yes, activate!',
  onOk: onOk,
  cancelText: 'Cancel',
})

const propertyButtonActionData = ({ showModal, hideModal, applicationsRefetch, property, refetch }) => [
  { 
    className: classNames(BASE_CLS, 'btn-primary'),
    label: 'Run App',
    onClick: () => showModal({ children: aAForm({ applicationsRefetch, property }) }),
  },
  { 
    className: classNames(BASE_CLS, 'btn-danger'), 
    label: 'Deactivate',
    onClick: () => showModal({ children: delConfirm({ onCancel: deactivateProperty({ property, hideModal, refetch }) }) }),
  },
]

export const propertyButtonActionDataInactive = ({ showModal, hideModal, applicationsRefetch, property, refetch }) => [
  { 
    className: classNames(BASE_CLS, 'btn-success'), 
    label: 'Reactivate Property',
    onClick: () => showModal({ children: actConfirm({ onOk: activateProperty({ property, hideModal, refetch }) }) }),
  },
]

export default propertyButtonActionData


