import React from 'react'

const addApplicationForm = [
  [{ render: () => (<h2 className="mb-2 mt-2">Run an Application</h2>)}],
  [{ render: () => (
    <div className="alert alert-success white-text">
      <p className="mt-2"><b>We will ask the applicant for:</b></p>
      <ul>
        <li>Photo ID</li>
        <li>3 Months of Paystubs</li>
      </ul>
      <p className="mt-1"><b>Once everything is complete you'll receive:</b></p>
      <ul>
        <li>Photo ID</li>
        <li>Paystubs</li>
        <li>Credit Report</li>
        <li>Criminal/Background Report</li>
      </ul>
    </div>
  )}],
  [{ name: 'applicantName', type: 'text', label: 'Applicant Name:', placeholder: 'John Appleseed' }],
  [{ name: 'email', type: 'email', label: 'Email:', placeholder: 'johnappleseed@apple.com' }],
  [{ render: ({ handleSubmit, isSubmitting, touched, errors }) => (
    <div>
      <button
        className="btn btn-primary col-md-12 mt-3"
        onClick={handleSubmit}
        children="Run"
        disabled={isSubmitting}
      />
      {touched && errors.form && (
        <p className="text-danger">{errors.form}</p>
      )}
    </div>
  )}],
]

export default addApplicationForm


